@import "../../sassStyles/_variables";


#pagesContainer {
  position: relative;
  width: calc(100% - 6vw);
  height: 100%;
  margin-left: 6vw;
  transition: 1s cubic-bezier(.65,.05,.36,1);
}

#backgroundShapes {
  position: fixed;
  width: 100%;
  height: 100%;
  background: url("../../assets/images/background_shapes.png") no-repeat 0 100%;
}

.page {
  position: relative;
  width: 100%;
  height: 100vh;

  &:not(:last-child) {
    margin-bottom: 100px;
  }

  &#about-page .content {
    height: 115%;
    width: 90%;
    bottom: -30%;
    right: 0;
    z-index: 1;
    background-color: $primary-middle-light-color-palette;
  }

  &#skills-page .content {
    height: 120%;
    width: 90%;
    bottom: -30%;
    left: -6vw;
    background-color: $primary-light-color-palette;
  }

  &#work-page .content {
    height: 130%;
    width: 90%;
    bottom: -30%;
    top: 10%;
    left: 50%;
    transform: translateX(-50%);
    background-color: $primary-middle-light-color-palette;
  }

  &#contact-page .content {
    height: 85%;
    width: 85%;
    right: 0;
    top: 50%;
    transform: translateY(-50%);
    background-color: $primary-light-color-palette;
  }

}

.content {
  position: absolute;
  background-color: $primary-middle-light-color-palette;
  padding: 20px;
  transition: 0.5s ease-in-out;

  .title {
    position: absolute;
    font-size: 4vw;
    font-family: "Josefin Sans", sans-serif;
    width: fit-content;

    &:after {
      content: "";
      width: 100%;
      height: 5px;
      background-color: #ea9a27;
      display: block;
      position: absolute;
      left: 0;
      transition: 0.5s ease-in-out;
    }

    &#about-title {
      top: 50px;
      left: 100px;
    }

    &#skills-title {
      bottom: 0;
      right: calc(6vw + 15%);
      transform: translateY(50%);
    }

    &#work-title {
      top: 50px;
      left: 50%;
      transform: translateX(-50%);
    }

    &#contact-title {
      top: 50px;
      left: 100px;
    }

  }

  #about-text {
    position: absolute;
    left: 100px;
    top: 40%;
    width: 45%;
    transform: translateY(-50%);
    font-size: 25px;
    line-height: 1.2;
  }

  #profile-picture {
    width: 300px;
    height: 300px;
    position: absolute;
    top: 40%;
    left: 75%;
    transform: translate(-50%, -50%);
    background-image: url("../../assets/images/profile_picture.jpg");
    background-position: center center;
    background-size: cover;
    box-shadow: 1px 1px 1px 0 $primary-dark-color-palette;

    &:after {
      content: "";
      position: absolute;
      top: 0;
      bottom: 0;
      left: 0;
      right: 0;
      background-color: $secondary-color-palette;
      transition: 0.6s cubic-bezier(.55,.06,.68,.19);
    }

  }

  #profile-picture.active:after {
    left: 100%;
  }


  #works-carousel {
    justify-content: center;
    position: absolute;
    display: flex;
    top: calc(50% - 75px);
    transform: translateY(-50%);
    flex-wrap: wrap;
    width: calc(100% - 40px);
    gap: 10px;

    .project {
      width: 17vw;
      height: 32vh;
      position: relative;
      overflow: hidden;
      box-shadow: 1px 1px 1px 0px $primary-dark-color-palette;

      .project-image {
        width: 100%;
        height: 100%;
        background-size: cover;
        background-position: center center;
        transform: scale(1.2);
        transition: 0.5s ease-in-out;


        &:after {
          content: "";
          width: 100%;
          height: 100%;
          position: absolute;
          background-color: $secondary-color-palette;
          transition: 0.5s ease-in-out;
          opacity: 0;
        }
      }

      .project-name {
        font-size: 35px;
        width: 100%;
        text-align: center;
        position: absolute;
        font-family: "Josefin Sans", sans-serif;
        font-weight: bold;
        color: $primary-dark-color-palette;
        top: 0;
        left: 50%;
        transform: translate(-50%, -100%);
        transition: 0.3s ease-in-out;
      }

      .project-description {
        font-size: 25px;
        width: 100%;
        text-align: center;
        position: absolute;
        font-family: "BarlowCondensed-Light", sans-serif;
        color: $primary-dark-color-palette;
        top: 100%;
        left: 50%;
        transform: translate(-50%, 100%);
        transition: 0.3s ease-in-out;
      }

      &.active {

        .project-image {
          transform: none;
        }

        &:hover {
          .project-name {
            top: 35%;
            transform: translate(-50%, -50%);
          }
          .project-description {
            top: 65%;
            transform: translate(-50%, -50%);
          }

          .project-image {
            transform: scale(1.2);

            &:after {
              opacity: 1;
            }
          }
        }
      }

    }

  }

  .contact-text {
    position: absolute;
    left: 100px;
    top: 55%;
    width: 75%;
    transform: translateY(-50%);
    font-size: 25px;
    line-height: 1.2;
  }

  input, textarea, button {
    background-color: #121e26;
    color: white;
    font-size: 18px;
    resize: none;
    font-style: normal;
    font-family: "BarlowCondensed-Light", sans-serif;
    padding: 10px;
    width: 100%;
    height: 100%;

    &:focus  {
      outline: 0;

      +.input-border {
        width: 100%;
      }
    }
  }

  .button {
    cursor: pointer;
    height: 50px;
    width: 35%;
    position: relative;
    transform: scale(1);
    transition: 0.1s ease-in-out;

    span:nth-child(2) {
      top: 0;
      left: 0;
      width: 100%;
      height: 3px;
      transform-origin: right;
      transition-delay: 0s;
    }
    span:nth-child(3) {
      top: 0;
      right: 0;
      width: 3px;
      height: 100%;
      transform-origin: bottom;
      transition-delay: 0.1s;
    }
    span:nth-child(4) {
      bottom: 0;
      right: 0;
      width: 100%;
      height: 3px;
      transform-origin: left;
      transition-delay: 0.2s;
    }
    span:nth-child(5) {
      bottom: 0;
      left: 0;
      width: 3px;
      height: 100%;
      transform-origin: top;
      transition-delay: 0.3s;
    }

    &:hover {
      .submit-border {
        transform: scale(1);
      }

      span:nth-child(2) {
        transform-origin: left;
      }
      span:nth-child(3) {
        transform-origin: top;
      }
      span:nth-child(4) {
        transform-origin: right;
      }
      span:nth-child(5) {
        transform-origin: bottom;
      }
    }

    .submit-border {
      transform: scale(0);
      background-color: $secondary-color-palette;
      position: absolute;
      transition: transform 0.1s linear;
    }

    &:active {
      transform: scale(0.95);
      background-color: $secondary-color-palette;
      color: $primary-dark-color-palette;
      font-weight: bold;
    }

  }

  .input-border {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 0%;
    height: 3px;
    background-color: $secondary-color-palette;
    transition: 0.2s ease-in-out;
  }

  .name-input, .email-input {
    width: 49%;
    height: 50px;
    position: relative;
  }

  .message-input {
    width: 100%;
    position: relative;
    height: 200px;
    margin: 2% 0;
  }

  .skill-box {
    background-color: $primary-middle-light-color-palette;
    position: absolute;
    transform: translate(-50%, -50%);
    text-align: center;
    transition: 0.3s ease-in-out;
    border: 2px solid $primary-middle-light-color-palette;

    &:hover {
      border: 2px solid $secondary-color-palette;
    }


    .skill-name {
      position: absolute;
      font-family: "Josefin Sans", sans-serif;
      top: 0;
      left: 0;
      transform: translate(0%, -90%);
      font-size: 30px;
      color: $secondary-color-palette;
    }

    .skill-description {
      font-size: 20px;
      position: relative;
      padding: 10px;
      min-height: 100px;
    }

    .skill-list {
      padding: 10px;
      font-size: 50px;
      font-family: "BarlowCondensed-Medium", sans-serif;
      display: flex;
      justify-content: space-evenly;
      width: 100%;
      height: 100px;

      .item {
        position: relative;
        width: 100%;
        display: flex;
        flex-direction: column;
        justify-content: center;
        gap: 5px;
        align-items: center;

        .item-name {
          font-size: 15px;
        }

        img {
          width: 50px;
          height: 50px;
        }
      }

    }


    &.backend-skills {
      top: 30%;
      left: 40%;
      width: 30%;
    }

    &.frontend-skills {
      top: 30%;
      left: 75%;
      width: 30%;
    }

    &.miscellaneous-skills {
      transform: none;
      top: 50%;
      left: 25%;
      width: 65%;
    }

  }

}

.mention {
  position: absolute;
  bottom: 10px;
  left: 50%;
  transform: translateX(-50%);
  font-size: 15px;
  color: #b3b3b3;
}

@media screen and (max-width: 992px) {
  #backgroundShapes {
    background-size: contain;
  }

  .page {

    &#about-page .content {
      height: calc(100% + 100px);
      width: calc(100% + 6vw);
      top: 0;
      z-index: 1;
    }

    &#skills-page .content {
      height: calc(100% + 100px);
      width: calc(100% + 6vw);
      top: 0;
    }

    &#work-page .content {
      height: calc(100% + 100px);
      width: calc(100% + 6vw);
      top: 0;
      left: -6vw;
      transform: none;
    }

    &#contact-page .content {
      height: calc(100% + 100px);
      width: calc(100% + 6vw);
      top: 0;
      transform: none;
    }

  }

  .content {
    padding: 10px;

    .title {
      font-size: 8vw;
      top: 20px;
      left: 50%;
      transform: translateX(-50%);
      white-space: nowrap;

      &:after {
        height: 2px;
      }

      &#about-title {
        top: 20px;
        left: 50%;
      }

      &#skills-title {
        bottom: -20px;
        top: initial;
        transform: translate(-50%, 100%);
      }

      &#work-title {
        top: 20px;
      }

      &#contact-title {
        top: 20px;
        left: 50%;
      }

    }

    #about-text {
      left: 50%;
      top: 300px;
      width: 70%;
      font-size: 2.5vh;
      transform: translateX(-50%);
      text-align: center;
    }

    #profile-picture {
      width: 175px;
      height: 175px;
      top: 100px;
      left: 50%;
      transform: translateX(-50%);
    }

    .skill-box {
      .skill-name {
        font-size: 18px;
        transform: translate(0%, -100%);
      }

      .skill-description {
        font-size: 20px;
        padding: 5px;
        min-height: unset;
      }

      .skill-list {
        font-size: 30px;
        justify-content: center;
        gap: 20px;
        height: auto;
        flex-wrap: wrap;

        .item {
          width: fit-content;

          .item-name {
            font-size: 15px;
          }

          img {
            width: 35px;
            height: 35px;
          }
        }

      }

      &.backend-skills {
        top: calc(25% - 25px);
        left: 50%;
        width: 70%;
      }

      &.frontend-skills {
        top: calc(50% - 50px);
        left: 50%;
        width: 70%;
      }

      &.miscellaneous-skills {
        top: calc(75% - 75px);
        left: 50%;
        width: 70%;
        transform: translate(-50%, -50%);
      }

    }

    #works-carousel {
      top: calc(50% - 20px);
      left: 8vw;
      width: calc(100% - 8vw);
      justify-content: center;

      .project {
        width: 30vw;
        height: 20vh;
        margin: 0;

        .project-name {
          font-size: 20px;
        }

        .project-description {
          font-size: 17px;
        }
      }

    }

    .contact-text {
      transform: none;
      top: 80px;
      left: 13vw;
      width: calc(100% - 13vw);
      font-size: 17px;
      line-height: 1.2;
      padding: 10px;

      a {
        button {
          width: 100%;
        }
      }
    }
  }

  .mention {
    bottom: 5px;
    font-size: 10px;
  }
}

@media screen and (max-width: 992px) and (orientation: landscape) {
  .content {
    .title {
      font-size: 7vh;
    }

    #about-text {
      top: calc(50% - 20px);
      left: 35%;
      width: 50%;
      transform: translate(-50%, -50%);
      font-size: 4vh;
    }

    #profile-picture {
      top: calc(50% - 50px);
      left: 80%;
      transform: translate(-50%, -50%);
    }

    #works-carousel {
      top: calc(50% - 20px);
      left: 2vw;
      width: calc(100% - 2vw);

      .project {
        width: 25vw;
        height: 25vh;

        .project-name {
          font-size: 20px;
        }

        .project-description {
          font-size: 17px;
        }
      }

    }

    .contact-text {
      transform: none;
      top: 120px;
      left: 8vw;
      width: calc(100% - 8vw);
      font-size: 17px;
      line-height: 1.2;
      padding: 10px;

    }

    .skill-box {
      .skill-list {
        font-size: 30px;
        gap: 40px;
      }
      &.backend-skills {
        top: calc(30% - 25px);
        left: 50%;
        width: 80%;
      }

      &.frontend-skills {
        top: calc(55% - 50px);
        left: 50%;
        width: 80%;
      }

      &.miscellaneous-skills {
        top: calc(80% - 75px);
        left: 50%;
        width: 80%;
      }

    }

  }
}